import React from 'react';
import logo from '../../assets/images/2gather.jpg';
import './style.scss';


class SignIn extends React.Component {

    state = {
        password: false,
    }

    togglePassword = () => {
        this.setState({
            password: !this.state.password,
        })
    }
    render(){
        const { password } = this.state;
        return (
            <div className="sign-in">
                <div className="sign-in-form">
                    <div className="sign-in-form-container">

                    

                <h2>Sign in</h2>
                        <p>Enter your email and password and login in your <br/> account</p>
                    <form>
                        <div className="G-field">
                        <label>
                            <input type="text"/>
                        </label>
                        <span className="input-border"></span>
                        </div>
                        <div className="G-field">
                        <label>
                            <input type={!password ? "password" : "text"}/>
                        </label>
                        <i className={!password ? "icon-ic_eye" : "icon-ic_eyeclosed"} onClick={this.togglePassword}></i>
                        </div>
                       

                        <button className="G-button">
                            Sign in
                        </button>
                    </form>
                </div>
                </div>
                <div className="logo">
                    <img src={logo} alt="2gather"/>
                </div>
            </div>
        )
    }
}

export default SignIn;